import service from "../../service.js";
export default {
	data() {
		return {
			img_arr: [],
			id:''
		}
	},
	onLoad(e) {
		// console.log(e);
		this.id = e.id
	},
	methods: {
		upload(){
			var self = this;
			uni.chooseImage({
			    success: (chooseImageRes) => {
			        const tempFilePaths = chooseImageRes.tempFilePaths;
			        uni.uploadFile({
			            url: service.api.service.uploadImg, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
			            filePath: tempFilePaths[0],
			            name: 'file',
			            success: (res) => {
			                console.log(JSON.parse(res.data),"asfasdf");
							const path = JSON.parse(res.data);//打印出来就知道为什么要用JSON.parse
							// self.invoiceimg = path.data.url;
							// console.log(path)
							self.img_arr.push(path.data.url)
							console.log(self.img_arr)
							uni.showToast({
								title:path.info,
								icon:'none'
							});
			            }
			        });
			    }
			});
		},
		close(index){
			this.img_arr.splice(index,1)
			console.log(this.img_arr)
		},
		Submi(){
			if(this.img_arr == ''){
				uni.showToast({
					icon:'none',
					title:'请上传POS机首刷凭证！'
				})
				return
			}
			let arr = []
			arr.push(this.img_arr)
			service.auth(this , service.api.service.posCheck , 'POST' , {
				pos_fields:arr,
				pos_id:this.id,
				token:uni.getStorageSync('token')
			} , function(self ,res){
				console.log(res);
				uni.showToast({
					icon:'none',
					title:res.info
				});
					setTimeout(function(){
						uni.switchTab({
							url:'/pages/main/my'
						})
					},1000)
				
			});
			
		},
	},
}
